body {
    margin: 0;
    background: $white;
    font-size: 18px;
    font-family: $primaryFont;

    @media (max-width: $mq-mobile) {
        font-size: 16px;
    }


    .container {
        width: 90%;
        margin: auto;
    }

    strong {
        font-family: $secondaryFont;
    }

    h2 {
        color: $primaryColor;
        font-size: 32px;
        text-align: center;
        position: relative;
        margin-bottom: 60px;
        font-family: $secondaryFont;

        &:after {
            content: "";
            width: 24px;
            height: 2px;
            background-color: $secondaryColor;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: -10px;
            border-radius: 50px;
        }
    }

    a {
        @include default-a;
    }
}