@mixin default-a {
    text-decoration: none;
    color: $primaryColor;
    font-family: $secondaryFont;
}

@mixin default-btn($size, $backgroundColor) {
    color: $white;
    background-color: $backgroundColor;
    font-size: $size;
    line-height: 16px;
    padding: 13px 14px;
    display: block;
    max-width: 114px;
    text-align: center;
    border-radius: 5px;
    transition: 0.3s;
}