$width_card: 378px;

.card-main {
    display: flex;
    margin: 40px auto;
    max-width: $width_card * 3 + 30 * 3;

    @media (max-width: $mq-tablet) {
        display: block;
        margin: auto;
    }


}

.card {
    opacity: 0;
    width: 100%;
    max-width: $width_card;
    margin: 0 15px;
    color: $black;
    background-color: $white;
    overflow: hidden;
    border-radius: 5px;
    -webkit-box-shadow: 0px 0px 19px -6px rgba(0, 0, 0, 0.38);
    -moz-box-shadow: 0px 0px 19px -6px rgba(0, 0, 0, 0.38);
    box-shadow: 0px 0px 19px -6px rgba(0, 0, 0, 0.38);

    @media (min-width: $mq-mobile) and (max-width: $mq-tablet) {
        max-width: 100%;
        display: flex;
    }

    @media (max-width: $mq-tablet) {
        margin: 0 auto 30px auto;
    }

    .card-image {
        overflow: hidden;

        @media (min-width: $mq-mobile) and (max-width: $mq-tablet) {
            min-width: $width_card;
        }
    }

    img {
        width: 100%;

        @media (min-width: $mq-mobile) and (max-width: $mq-tablet) {
            height: 100%;
            width: auto;
        }

    }

    h3 {
        font-size: 26px;
        color: $primaryColor;
        position: relative;

        &:after {
            content: "";
            width: 24px;
            height: 2px;
            background-color: $secondaryColor;
            position: absolute;
            left: 0;
            bottom: -10px;
            border-radius: 50px;
        }
    }

    .card-container {
        width: 90%;
        margin: 0 20px;
        height: 303px - 20;
        padding-bottom: 20px;
        display: flex;
        justify-content: space-between;
        flex-direction: column;


        @media (min-width: $mq-mobile) and (max-width: $mq-tablet) {
            height: auto;
            min-height: 220px;
        }

        @media (max-width: $mq-mobile) {
            height: auto;
            min-height: 220px;
        }

        a {
            @include default-a;
            position: relative;

            &:after {
                content: "";
                background: url("../images/arrow.svg") no-repeat center;
                width: 8px;
                height: 13px;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                padding-left: 10px;
                transition: .3s;
            }

            &:hover::after {
                padding-left: 20px;
            }

        }

    }
}