header {
    overflow: hidden;
    margin: 60px 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    background: url("../images/background.jpg") no-repeat left;
    height: 535px;

    @media (max-width: $mq-mobile) {
        background-position: center;
        align-items: center;
    }
}

.header-card {
    opacity: 0;
    background-color: $white;
    max-width: 572px - 34*2;
    border-radius: 5px;
    color: $black;
    -webkit-box-shadow: 0px 0px 19px -6px rgba(0, 0, 0, 0.38);
    -moz-box-shadow: 0px 0px 19px -6px rgba(0, 0, 0, 0.38);
    box-shadow: 0px 0px 19px -6px rgba(0, 0, 0, 0.38);
    padding: 45px 34px;
    margin-right: 60px;

    @media (max-width: $mq-mobile) {
        width: 80%;
        padding: 25px 24px;
        margin: 0 30px;
        font-size: 15px;
    }

    h3 {
        margin-top: 0;
        color: $primaryColor;
        font-size: 38px;
        position: relative;

        @media (max-width: $mq-mobile) {
            font-size: 26px;
        }

        &:after {
            content: "";
            width: 30px;
            height: 2px;
            background-color: $secondaryColor;
            position: absolute;
            left: 0;
            bottom: -10px;
            border-radius: 50px;
        }
    }

    p {
        margin: 25px 0 35px 0;
    }

    .header-card_button {
        display: flex;
    }

    .button {
        @include default-btn(16px, $secondaryColor);
        margin-right: 12px;

        &:hover {
            @include default-btn(16px, #745424);
        }
    }

    .button_2 {
        @include default-btn(16px, $primaryColor);

        &:hover {
            @include default-btn(16px, #CE6B43);
        }
    }
}